html{font-family:Arial, Helvetica, sans-serif ;
overflow-x: hidden;}
body{padding-top:50px;
text-align: center;}


.shop{
width:86%;
margin-left:100px;
padding:25px 0px 0px 0px;
text-align: center;
}

.heroimg{width: 300px;
height:350px;
object-fit:fill;

margin: 45px 0px 0px 10px;}

.productdiv{display:inline-table;
  width: 400px;
  height:650px;
  background-color: rgb(237, 235, 235);
  margin: 30px 15px 0px 15px;
  text-align: center ;
 
}

.title{max-width: 90%;
 height:50px;
text-align: center;

margin-left: 20px;}

.addtocartbtn{width: 70%;
height: 50px;
font-weight: bolder;}

.price::before{content: "$";}

.filterbtn{width:200px;
height:50px;
margin:0px 5px 0px 5px}

.cartpage{background-color: #ffffff;
min-height: 100vh;
width: 80vh;
position:absolute;
top:1px;
right:1px;
border-style:dashed;
border-width: 0px 0px 1px 1px;}

.hidden{visibility: hidden;}

.cartproductdiv{background-color: rgb(237, 235, 235);
height :120px;
margin: 5px 0px  5px 20px;}

.cartheroimg{height: 90px;
  width:70px;
position: relative;
right: 230px;
top:15px}

.carttitle{position: relative;
right:-150px;
bottom:100px;
width:350px;
height:50px
}

.cartprice{position: relative;
  right:-250px;
  bottom:150px}
  .cartprice::before{content: "$";}

  .removefrmcartbtn{position: relative;
    right:265px;
    bottom:230px;
    background-color: inherit;
    border-radius: 50px}

   .qtybtn{position: relative;
    right:-240px;
    bottom:200px}

    .qty{position: relative;
      right:0px;
      bottom:170px}

      .qty::before{content:"QTY:" ;
      }
      
      #totalvalue::before{
        content: "Cart Total : $";
      }
      .cross{position: absolute;
        top:30px;
      left:30px;
      width: 30px;
      height:30px;
      background-color: inherit;
      border-radius: 50px;
  }
  #checkout{width:50%;
    height:50px;
    margin-bottom: 50px;
  }